*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.age-card{
    background-color: rgb(168, 37, 129);
    width: 90%;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: whitesmoke;
    border-radius: 30px;
    padding: 30px 0;
    box-shadow: 1px 5px 20px rgba(82, 82, 82, 0.671);
}

.age-head{
    padding-bottom: 20px;
}

.age-list{
    font-size: large;
    line-height: 30px;
    text-align: center;
}

.centering{
    display: flex;
    justify-content: center;
    margin-top: 200px;
}


li{
    margin-bottom: 10px;
}
