*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.navbar{
    width: 100%;
    display: flex;
    padding: 10px 25px;
    align-items: baseline;
    justify-content: space-between;
    background-color: rgb(168, 37, 129);
    color: whitesmoke;
    font-family: Arial, Helvetica, sans-serif;
}

a{
    text-decoration: none;
    color: whitesmoke;
}

.nav-links{
    display: flex;
    list-style: none;
}

.ind-nav-link-1{
    margin: 0 10px;
}

img{
    width: 100%;
}

.btn-center{
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.btn-test{
    padding: 10px 18px;
    font-size: 23px;
    background-color: rgb(168, 37, 129);
    color: whitesmoke;
    cursor: pointer;
    border: none;
    border-radius: 20px;
}

.btn-test:hover{
    background-color: rgb(168, 37, 129);
}

.out-obj{
    font-family: Arial, Helvetica, sans-serif;
    color: whitesmoke;
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.in-obj{
    background-color: rgb(168, 37, 129);
    width: 90%;
    text-align: center;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 1px 5px 20px rgba(82, 82, 82, 0.671);
}

.obj-head{
    margin-bottom: 10px;
}

hr{
    border: 1px solid whitesmoke;
    margin-bottom: 10px;
}

.writings{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 20px 25px;
    color: rgb(39, 39, 39);
}

.write-satla-subhead{
    margin-top: 20px;
}

.list-styles{
    list-style-type: lower-roman;
    margin: 20px 0;
    margin-left: 20px;
}

.list-styles-fsize{
    font-weight: bold;
}

.admin-back{
    background-color: rgb(168, 37, 129);
    padding: 30px 0;
}

.administer{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 20px 25px;
    color: whitesmoke;
}

.admin-head{
    margin-bottom: 10px;
}

.admin-list{
    margin: 10px 0;
    font-size: large;
    font-weight: bold;
}

.footer-sect{
    background-color: rgb(168, 37, 129);
    text-align: center;
    color: whitesmoke;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

footer{
    padding: 30px 0;
}

.btn-contact{
    margin-top: 20px;
    background-color: white;
    border: none;
    padding: 10px 15px;
}

.link-contact{
    text-decoration: none;
    color: rgb(168, 37, 129);
}

.copyright{
    margin-bottom: 0;
}