* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.navbar {
    width: 100%;
    display: flex;
    padding: 10px 25px;
    align-items: baseline;
    justify-content: space-between;
    background-color: rgb(168, 37, 129);
    color: whitesmoke;
    font-family: Arial, Helvetica, sans-serif;
}

a {
    text-decoration: none;
    color: whitesmoke;
}

.heading {
    text-align: center;
}

.question-sect {
    margin: 20px 0;
    width: 92%;
    margin-left: 5px;
}

.question {
    font-weight: bold;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.btn-section {
    text-align: center;
}

.btn-ach,
.btn-gl {
    padding: 7px 14px;
    cursor: pointer;
    border: none;
    color: whitesmoke;
}

.note {
    color: rgb(168, 37, 129);
}

.btn-ach {
    margin-right: 10px;
    background-color: green;
}

.btn-gl {
    background-color: red;
}

.btn-center {
    display: flex;
    justify-content: center;
}

.btn-sum {
    font-size: larger;
    padding: 8px 17px;
    border-radius: 10px;
    border: none;
    background-color: rgb(168, 37, 129);
    color: whitesmoke;
    margin-bottom: 30px;
    cursor: pointer;
}

ul {
    padding: 20px 5px;
}

.summary-sect {
    background-color: rgb(168, 37, 129);
    color: whitesmoke;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.newhr {
    border-top: 1px solid whitesmoke;
}